
.pagination-container {
    margin-right: auto;
    margin-left: auto;
    padding-bottom: 60px;
}

.tui-pagination {
    max-width: 280px;
    height: 40px;
    margin-right: auto;
    margin-left: auto;
    line-height: normal;
    text-align: center;
    font-size: 0;
}

 .tui-page-btn {
    display: inline-block;
    position: relative;
    font-family: 'Roboto';
    width: 40px;
    margin-left: 0;
    color: #000000;
    font-size: 12px;
    font-weight: normal;
    line-height: 40px;
    text-decoration: none;
    vertical-align: middle;
    border: none;
    
}

 .tui-pagination strong {
    color: #fff;
    background: #B92F2C;
    border-radius: 5px;
    cursor: default;
}


.tui-pagination .tui-prev, .tui-pagination .tui-next {
    width: 40px;
    overflow: hidden;
    margin: 0px;
    padding: 0;
    font-size: 0;
    background-color: #F7F7F7;
    line-height: 40px;
    white-space: nowrap;
    font-weight: normal;
    border-radius: 5px;
    border: none;
}


 .tui-pagination .tui-ico-prev {
    background: url(/src/images/pagination-svg/arrow-left.svg);
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
    width: 16px;
    height: 16px;
    order: -1;
    color: #000000;
    background-repeat: no-repeat;
    background-position: center;
}

.tui-pagination .tui-ico-next {
    background-image: url(/src/images/pagination-svg/arrow-right.svg);
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    width: 16px;
    height: 16px;
    order: 1;
    color: #000000;
    background-repeat: no-repeat;
    background-position: center;
}

@media screen and (max-width: 767px) {
    .tui-next-is-ellip,
    .tui-prev-is-ellip,
    .tui-first,
    .tui-last {
        display: none;
    }
}




@media screen and (min-width: 768px) {

    .tui-ico-ellipsis, .tui-ico-first, .tui-ico-last {
        overflow: hidden;
    }

    .tui-pagination {
        max-width: 374px;
        height: 40px;
        margin-right: auto;
        margin-left: auto;
        line-height: normal;
        text-align: center;
        font-size: 0;
    }

    .tui-pagination .tui-first, .tui-pagination .tui-last {
        overflow: hidden;
        margin: 0px;
        padding: 0;
        font-size: 0;
        background-color: #F7F7F7;
        line-height: 40px;
        white-space: nowrap;
        font-weight: normal;
        
        border: none;
    }

    .tui-pagination .tui-prev, .tui-pagination .tui-next {
        width: 36px;
        height: 40px;
        overflow: hidden;
        margin: 0px;
        padding: 0;
        font-size: 0;
        background-color: #ffffff;
        line-height: 40px;
        white-space: nowrap;
        font-weight: normal;
        border: none;
    }


    .tui-pagination .tui-prev-is-ellip {
        width: 11px;
        border-right: 0
    }
    
    .tui-pagination .tui-next-is-ellip {
        width: 11px;
        border-left: 0
    }


    .tui-pagination .tui-ico-first, .tui-pagination .tui-ico-last, .tui-pagination .tui-ico-ellip {
        background: url(data:image/png;base64,) no-repeat 0 0;
        display: inline-block;
        vertical-align: middle;
        font-size: 0px;
    }

    .tui-pagination .tui-ico-first {
        width: 11px;
        height: 10px;
        background-position: 0px 0px
    }

    .tui-pagination .tui-ico-last {
        width: 11px;
        height: 10px;
        background-position: 0px -12px
    }

    .tui-pagination .tui-ico-ellip {
        width: 10px;
        height: 2px;
        margin-top: 5px;
        vertical-align: top;
        background-position: 0px -24px
    }

    .tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
        padding: 0;
        line-height: 1;
    }

    
    .tui-pagination .tui-is-disabled .tui-ico-first {
        background-position: -13px 0px
    }
    
    .tui-pagination .tui-is-disabled .tui-ico-last {
        background-position: -13px -12px
    }

    

}